:root {
    --color-background: #f5f5f5;
    --color-text: #333333;
    --color-text-light: #a7a7a7;
    --color-tag: #556677;
    --color-link: #0060A0;
    --color-accent: #b03000;
    --xprmntl-font:
        'xprmntl',
        -apple-system,
        BlinkMacSystemFont,
        'Segoe UI',
        Roboto,
        Oxygen-Sans,
        Ubuntu,
        Cantarell,
        'Helvetica Neue',
        sans-serif;
}

@media (prefers-color-scheme: dark) {
    :root {
        --color-background: #333333;
        --color-text: #f5f5f5;
        --color-link: #0085c7;
        --color-tag: #7F99AF;
        --color-accent: #e23300;
    }
}

body {
    background-color: #f5f5f5;
    background-color: var(--color-background);
    color: #333333;
    color: var(--color-text);
    margin: 0;
    padding: 0;
    font-family:
        -apple-system,
        BlinkMacSystemFont,
        'Segoe UI',
        'Roboto',
        'Oxygen',
        'Ubuntu',
        'Cantarell',
        'Fira Sans',
        'Droid Sans',
        'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
    font-family: 'xprmntl';
    src:
        url(/static/media/xprmntl.6d87ee91.woff2) format('woff2'),
        url(/static/media/xprmntl.4709be89.woff) format('woff'),
        url(/static/media/xprmntl.c9b44e2d.ttf) format('truetype');
}

a {
    color: #0060A0;
    color: var(--color-link);
    -webkit-transition: color 0.2s ease-in-out;
    transition: color 0.2s ease-in-out;
}

a:hover, a:active, a:focus {
    color: #b03000;
    color: var(--color-accent);
}

@media (prefers-color-scheme: dark) {
    img {
        opacity: 0.75;
        -webkit-transition: opacity .5s ease-in-out;
        transition: opacity .5s ease-in-out;
    }

    a:hover img,
    img:hover {
        opacity: 1;
    }
}

